<template>
  <div class="personal-content-body">
    <div class="ipmhome-content">
      <div class="titlebox">
        <div class="breadcrumb">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home/investProjectManageList?page=' + page }">项目管理</el-breadcrumb-item>
            <el-breadcrumb-item>{{projInfo.name}}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="title-name">
          {{projInfo.name}}
          <span :class="'title-name-span'+projInfo.type" v-if="projInfo.type_name!=undefined">{{projInfo.type_name}}</span>
        </div>
      </div>
      <div class="module-box">
        <div class="module-item" v-for="(item,index) in modules" :key="index" @click="gotoDetail(item)">
          <div class="module-item-title">
            {{item.title}}
          </div>
          <div class="module-item-title1">
            {{item.title1}}
          </div>
          <div class="module-item-number">
            {{item.number}}{{item.numbertext}}
          </div>
          <div class="module-item-img">
            <img class="img1" src="../../images/invest_homepage_module_iconbg.png">
            <img class="img2" :src="'../static/images/invest_homepage_module_icon'+index+'.png'">
          </div>
          <div class="module-item-linkbox" v-if="item.type==2">
            <div @click="gotoDetail(item1)" class="module-item-linkitem" v-for="(item1,index1) in item.modules" :key="index1">{{item1.name}}</div>
          </div>
          <div v-if="item.type==1" class="module-item-link">查看详情></div>
          <div class="module-item-wavebox">
            <div class="module-item-waveitem">
              <img class="img1" src="../../images/invest_homepage_wave3.png" alt="">
              <img class="img2" src="../../images/invest_homepage_wave4.png" alt="">
            </div>
            <div class="module-item-waveitem">
              <img class="img1" src="../../images/invest_homepage_wave1.png" alt="">
              <img class="img2" src="../../images/invest_homepage_wave2.png" alt="">
            </div>
          </div>
        </div>
      </div>
      <div class="companyinfo-box">
        <company-background></company-background>
      </div>
      <div class="companyinfo-box">
        <invest-project-manage-home></invest-project-manage-home>
      </div>
    </div>
  </div>
</template>
<script>
import companyBackground from '../../pages/companyBackground/companyBackground.vue'
import investProjectManageHome from '../../pages/investmentManage/investProjectManageHome.vue'
import { rootDirInfo, getBtCompangUser, approveNum, projectLsit } from '../../config/api'
import {
  projOutline
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import api from '../../config/url'; //引用url.js
export default {
  data() {
    return {
      data: [],
      projId: '',
      projInfo: '',
      modules: [{
        type: 1,
        title: '项目文件管理',
        title1: '共包含',
        number: 0,
        numbertext: '个文件',
        routername: 'investFolderManage'
      }, {
        type: 1,
        title: '审批管理',
        title1: '共包含',
        number: 0,
        numbertext: '个审批',
        routername: 'applyListNew',
      }, {
        type: 1,
        title: '联系人管理',
        title1: '共包含',
        number: 0,
        numbertext: '个联系人',
        routername: 'investedCompanyContactManage',
      }, {
        type: 2,
        title: '投后管理',
        title1: '包含',
        modules: [{
          type: 1,
          name: '被投企业账号管理',
          routername: 'investedCompanyAccountManage'
        }, {
          type: 1,
          name: '被投企业公告发布',
          routername: 'investedPublishNews'
        }, {
          type: 1,
          name: '被投企业大事日志',
          routername: 'investedCompanyEventloglist'
        }]
      }],
      page: 1,
    }
  },
  components: {
    companyBackground,
    investProjectManageHome
  },
  created() {
  	if (this.$route.query.page) {
    		this.page = this.$route.query.page;
    }
    if (this.$route.query.id == undefined || this.$route.query.id == '') {
      this.$router.push({
        name: 'investProjectManageList'
      })
    } else {
      this.projId = this.$route.query.id;
      this.initData();
    };
  },
  mounted() {

  },
  methods: {
    initData() {
      projectLsit({
        page: 1,
        number: 10,
        id: this.projId
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.projInfo = res.data.payload.data[0]
        }
      })
      rootDirInfo({
        type: 1,
        id: this.projId,
        search: ''
      }).then(res => {
        if (res.data.msgNo == 1) {
          // let t = res.data.payload.total;
          let payload = res.data.payload;
          let count = 0;
          if(payload.data){
            payload.data.forEach(element => {
              count+=element.count;
            });
          }
          this.numberanimate(count, this.modules[0])
        }
      })
      getBtCompangUser({
        page: 1,
        number: 10,
        projectId: this.projId
      }).then(res => {
        if (res.data.msgNo == 1) {
          let g = res.data.payload.total;
          this.numberanimate(g, this.modules[2])
        }
      })
      approveNum({
        company_id: this.projId
      }).then(res => {
        if (res.data.msgNo == 1) {
          let g = res.data.payload.total;
          this.numberanimate(g, this.modules[1])
        }
      })
    },
    numberanimate(num, result) {
      let time = 500 / num;
      let timer = setInterval(function() {
        if (result.number < num) {
          result.number++;
        } else {
          clearInterval(timer);
        }
      }, time);
    },
    //跳转至详情页
    gotoDetail(item) {
      if (item.type == 1) {
        // if(item.routername == 'investFolderManage'){
        //   let params = {
        //     phone: '17802118897',
        //     return_url: '/home/investProjectManageHomepage',
        //     show_name: '默认文件夹',
        //     domain:api.domiainUrl,
        //     seprate_word_sg:1,
        //     page:this.$route.query.page,
        //     id:this.$route.query.id,
        //   }
        //   let url = '';
        //   let newArr = Object.keys(params);
        //   newArr.forEach((element, index) => {
        //     // console.log(params[element]);
        //     let str = element + '=' + params[element];
        //     if (index != newArr.length - 1) {
        //       str = str + '&';
        //     }
        //     url += str;
        //   });
        //   console.log(api.sourceDataUrl+'/api/cloud/autoLoad?'+url);
        // }else{
          this.$router.push({
            name: item.routername,
            query: {
              projectId: this.projId,
              from: 2
            }
          })
        // }
        
      }

    }
  }
};

</script>
<style lang="scss" scoped>
@import "../../style/mixin.scss";
.ipmhome-content {
  position: fixed;
  top: 60px;
  bottom: 0px;
  left: 224px;
  right: 0px;
  min-width: 100px;
  min-height: 100px;
  padding: 0px 20px;
  width: auto;
  overflow: auto;
  background: #F3F6FB;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1) inset;
}

.titlebox {
  width: 100%;
  background: #fff;
  padding: 30px;
  margin-top: 20px;
  box-shadow: 0px 0px 40px 0px rgba(215, 225, 246, 1);
  .title-name {
    font-size: 24px;
    color: #35383A;
    line-height: 1em;
    margin-top: 28px;
    font-weight: 500;
    display: flex;
    align-items: center;
    &>span {
      padding: 2px 5px;
      line-height: 1em;
      font-size: 14px;
      color: #F5C423;
      border: 1px solid #F5C423;
      margin-left: 10px;
    }
    .title-name-span1 {
      color: #F59523;
      border-color: #F59523;
    }
    .title-name-span2 {
      color: #3CB6C3;
      border-color: #3CB6C3;
    }
    .title-name-span4 {
      color: #F5C423;
      border-color: #F5C423;
    }
    .title-name-span5 {
      color: #7F4F9C;
      border-color: #7F4F9C;
    }
    .title-name-span6 {
      color: #1787FB;
      border-color: #1787FB;
    }
    .title-name-span7 {
      color: #7F4F9C;
      border-color: #7F4F9C;
    }
  }
}

.module-box {
  width: 100%;
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
  .module-item {
    width: 24%; //flex-grow:1;
    background: #fff;
    box-shadow: 0px 0px 10px 0px rgba(215, 225, 246, 1);
    padding: 18px 20px;
    position: relative;
    border: 1px solid rgba(215, 225, 246, 1);
    transition: all 0.3s;
    cursor: pointer;
    box-sizing: border-box;
    &:hover {
      border: 1px solid #3C98E8;
      box-shadow: 0px 0px 10px 0px rgba(60, 152, 232, 1); //transform:scale(1.02);
      .module-item-link {
        color: #3C98E8
      }
    }
    &:last-child {
      margin-right: 0px;
    }
    .module-item-title {
      font-size: 16px;
      color: #35383A;
      line-height: 1em;
    }
    .module-item-title1 {
      font-size: 14px;
      color: #A0A4AA;
      margin-top: 18px;
      line-height: 1em;
    }
    .module-item-number {
      font-size: 32px;
      line-height: 1em;
      color: #3C98E8;
      margin-top: 6px;
    }
    .module-item-link {
      font-size: 12px;
      width: 100%;
      display: block;
      color: #A0A4AA;
      line-height: 1em;
      padding-top: 12px;
      border-top: 1px solid #F1F1F1;
      margin-top: 20px;
    }
    .module-item-img {
      position: absolute;
      right: 20px;
      top: 20px;
      width: 54px;
      height: 62px;
      .img1 {
        position: absolute;
        animation: rotatetype 5s infinite linear;
      }
      .img2 {
        @include center;
      }
    }
    .module-item-wavebox {
      width: 100%;
      overflow: hidden;
      height: 20px;
      position: absolute;
      bottom: 0px;
      left: 0px;
    }
    .module-item-waveitem {
      display: flex;
      position: absolute;
      animation: infinitescroll1 20s infinite linear;
      left: 0px;
      bottom: 0px;
      img {
        height: 20px;
      }
      &:last-child {
        animation: infinitescroll 20s infinite linear;
      }
    }
    .module-item-linkbox {
      margin-top: 12px;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      .module-item-linkitem {
        padding: 5px 8px;
        font-size: 12px;
        line-height: 1em;
        color: #3C98E8;
        border: 1px solid #3C98E8;
        cursor: pointer;
        margin-right: 8px;
        margin-bottom: 8px;
        &:hover {
          background: #3C98E8;
          color: #fff;
        }
      }
    }
  }
}

.companyinfo-box {
  margin-top: 20px;
  box-shadow: 0px 0px 40px 0px rgba(215, 225, 246, 1);
}

@keyframes rotatetype {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes infinitescroll {
  0% {
    transform: translate3d(0px, 0px, 0);
  }
  25% {
    transform: translate3d(-250px, 8px, 0);
  }
  50% {
    transform: translate3d(-500px, 0px, 0);
  }
  75% {
    transform: translate3d(-250px, 8px, 0);
  }
  100% {
    transform: translate3d(0px, 0px, 0);
  }
}

@keyframes infinitescroll1 {
  0% {
    transform: translate3d(0px, 0px, 0);
  }
  25% {
    transform: translate3d(-250px, 4px, 0);
  }
  50% {
    transform: translate3d(-500px, 0px, 0);
  }
  75% {
    transform: translate3d(-250px, 4px, 0);
  }
  100% {
    transform: translate3d(0px, 0px, 0);
  }
}

</style>



